.CardTrend{
  background-color: aliceblue;
  border-radius:5px ;
  -webkit-border-radius:5px ;
  -moz-border-radius:5px ;
  -ms-border-radius:5px ;
  -o-border-radius:5px ;
  padding-bottom: 20px;
  width: 360px;
  
}
.imgs img{
  width: 360px;
  border-radius:5px ;
  -webkit-border-radius:5px ;
  -moz-border-radius:5px ;
  -ms-border-radius:5px ;
  -o-border-radius:5px ;
}
.iconsT{
  display: flex;
  margin-top: 12px;
  color: #7D7D7D;
    text-align: center;
    font-size: 16.809px;
    font-weight: 400;
   
}
.iconsT .icon-1{
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: 1px;
}
.iconsT .icon-1 img{
  width: 25.74px;
  height: 24px;
  padding-left: 35px;

}
.iconsT .icon-1 p{
padding-left: 10px;
color: #7D7D7D;
text-align: center;
font-family: Poppins;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: normal;
text-transform: capitalize;
}
.title{
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: -20px;
  margin-bottom: -20px;
  margin-left: -22px;
}
.para{
  padding: 40px;
  color: #5E6282;
font-family: Poppins;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 21.4px; /* 152.857% */
letter-spacing: 0.7px;
}
.flag{
  position: relative;
  /* border-radius: 50px;
border: 5px solid #FFF;
width: 50px;
height: 50px;
bottom: 20px;
left:20px ; */
 
}
.flag img{
  position: absolute;
  bottom: -15px;
  left:20px ;
}
.bttn{
  display: flex;
  width: 128.291px;
  height: 45px;
margin-left:28px ;

  justify-content: center;
  align-items: center;
  gap: 7.982px;
  border-radius: 4px;
background: #DF6951;
margin: auto;
}
.bttn a{
  color: var(--white, #FFF);
text-align: center;
font-family: Poppins;
font-size: 12px;
font-weight: 600;
  text-decoration: none;

}
@media screen and (max-width:400px) and (min-width:0px) {
  .CardTrend{
  width: 300px;
     }
     .imgs img{
      width: 300px;
    }
    .title{
      margin-left: 3px;
    }
}
@media (max-width: 1000px){
  .iconsT{
    flex-wrap: wrap;
  }
  .title h2{
    font-size: 22px;
  max-width: 100%;
  }
 
  
  
}