.promotion {
position: relative;
width: 100%;
}
.postion{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); 
}
.postion h2{
  color: #FFEFEC;
  text-align: center;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
}
.postion h1{
  color: #FFF;
  text-align: center;
  font-family: Volkhov;
  font-size: 40px;
  font-weight: 700;;
}
.postion .btnP{
  display: flex;
  justify-content: center;
  align-items: center;
width: 183px;
height: 58px;
background-color: transparent;
border: 2px solid white;
margin: auto;
}
.postion .btnP a{
  color:  #FFF;
text-align: center;
font-family: Poppins;
font-size: 15px;
font-style: normal;
font-weight: 600;
line-height: normal;
text-decoration: none;
}

.promotion img{
  width: 100%;

}
@media (max-width: 1000px){
  .promotion img{
     width:100%;
  }
  .postion h1{
font-size: 30px;
  }
  .postion h2{
font-size: 14px;
  }
  .postion .btnP{
    display: flex;
  width: 150px;
  height: 40px;
  margin: auto;
}
  }

  @media (max-width: 320px){
